@import url("https://cdnjs.cloudflare.com/ajax/libs/normalize/8.0.1/normalize.min.css");
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:ital,wght@0,300;0,500;0,600;0,700;1,400&display=swap');
@import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.2/css/all.min.css");
@import url('https://fonts.googleapis.com/css2?family=Fira+Code:wght@400;500;600;700&family=Fira+Mono:wght@400;500;700&family=Roboto+Mono:wght@400;500;600;700&display=swap');

$yldGrey: #181b1e;
$yldGold: #efbc86;
$green: #6ccda5;
$blue: #51b0dc;

$black: #121212;
$highlightBlue: #3b88fd;
$lightGrey: #ebebeb;

$sans: 'Inter', sans-serif;
$mono: 'IBM Plex Mono', monospace;
$fira: 'Roboto Mono', monospace;

html {
  height: -webkit-fill-available;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-variant-numeric: tabular-nums;
  background: lighten($yldGrey,6%);
  background: linear-gradient(0deg, rgba(lighten($yldGrey,7.5%),1) 0%, rgba(lighten($yldGrey,10%),1) 100%);
  background: linear-gradient(0deg, rgba(#000000, 1) 0%, rgba(#181818, 1) 100%);
  //background: linear-gradient(0deg, rgba(lighten(#000000,3.5%), 1) 0%, rgba(lighten(#181818,3.5%), 1) 100%);

  @media screen and (max-width: 640px){
    font-size: 14px;
  }
}

*, *::before, *::after {
  box-sizing: border-box;
}

.invisible {
  opacity: 0;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

body {
  display: flex;
  flex:0 0 auto;
  flex-direction: column;
  align-items: center;
  margin: 2rem 0;

  // background: url("../img/dark_dotted.png");
  background-repeat: repeat;

  font-weight: 400;
  font-size: 16px;
  font-family: $sans;
  color: #fff;

  @media screen and (max-width: 700px){
    margin: 0;
  }
}

.number {
  font-family: $mono;
  font-family: $fira;
  font-size: 1.025em;
}

.wrapper {
  width: 700px;
  max-width: 100%;
  display: flex;
  flex:0 0 auto;
  flex-direction: column;
  align-items: center;
  height: -webkit-fill-available;

  border: 1px solid rgba(#fff,0.15);
  border-radius: 0.5rem;
  box-shadow: 0 0 1rem rgba(#fff,0.025);

  background: linear-gradient(0deg, rgba(lighten($yldGrey,2%),1) 0%, rgba(lighten($yldGrey,5%),1) 100%);
  background: linear-gradient(0deg, rgba(#000000, 1) 0%, rgba(#181818, 1) 100%);
  // background: linear-gradient(0deg, rgba($yldGrey,1) 0%, rgba(lighten($yldGrey,5%),1) 100%);

  @media screen and (max-width: 640px){
    border: none;
    border-radius: 0;
  }
}

.top {
  display: flex;
  justify-content: center;
  width: 100%;
  padding: 1rem 2rem;
  border-bottom: 1px solid rgba(#fff,0.15);

  h1 {
    margin: 0;
    font-weight: 500;
  }

  a {
    border-radius: 50%;
    width: 3rem;
    height: 3rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid rgba($yldGold,0);
    transition: all 0.2s ease-out;

    svg {
      fill: $yldGold;
      width: 1.5rem;
      height: 1.5rem;
    }

    &:hover,
    &:focus {
      border-color: rgba($yldGold,0.5);
    }
  }
}

.toggle {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;

  nav {
    border-radius: 0.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid rgba(#fff,0.1);
    overflow: hidden;

    a {
      background: lighten(#181818,2.5%);
      color: #fff;
      font-weight: 600;
      font-size: 1.25rem;
      text-transform: capitalize;
      text-align: center;
      padding: 0.75rem 1.5rem;
      display: block;
      text-decoration: none;

      &.active {
        background: $yldGold;
        color: $yldGrey;
      }
    }
  }
}

.middle {
  display: flex;
  flex: 1;
  align-items: flex-start;
  width: 100%;
  padding: 1rem 1.5rem;
  position: relative;
  overflow: auto;

  &:after {
    content: '';
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 2rem;
    background: rgb(0,0,0);
    background: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(18,18,18,0) 100%);
  }

  .inside {
    position: relative;
    display: flex;
    width: 100%;
    overflow: auto;
    padding: .5rem 0 0 0;
  }

  ul {
    width: 100%;
    display: grid;
    grid-row-gap: 1.5rem;

    li {
      .borrow {
        position: relative;
        display: flex;
        justify-content: flex-end;

        .reward {
          background: $yldGold;
          color: $yldGrey;
          border-radius: 0.5rem;
          padding: 0.75rem;
          width: 7rem;

          display: flex;
          flex-direction: column;
          align-items: center;
          position: absolute;
          left: 0;
          top: 0;
          transform: translateY(-.5rem);

          span {
            display: inline-block;

            &.amount {
              font-weight: 700;
              font-size: 1.5rem;
              &:after {
                content: ' YLD';

              }
            }
            &.value {
              font-size: 0.875rem;
              opacity: 0.75;
              margin-top: 0.25rem;
            }
          }
        }
      }

      .lend {
        position: relative;
        display: flex;
        justify-content: flex-end;

        .reward {
          background: $yldGold;
          color: $yldGrey;
          border-radius: 0.5rem;
          padding: 0.75rem;
          width: 7rem;

          display: flex;
          flex-direction: column;
          align-items: center;
          position: absolute;
          left: 0;
          top: 0;
          transform: translateY(-.5rem);

          span {
            display: inline-block;

            &.amount {
              font-weight: 700;
              font-size: 1.5rem;
              &:after {
                content: '%';

              }
            }
            &.value {
              font-size: 0.875rem;
              opacity: 0.75;
              margin-top: 0.25rem;
            }
          }
        }
      }

      .info {
        background: lighten(#181818,2.5%);
        border-radius: 0.5rem;
        border: 1px solid rgba(#fff,0.05);
        width: calc(100% - 6rem);
        padding: 0.75rem;
        padding-left: 2.5rem;

        display: grid;
        grid-template-columns: 1fr 1fr;
        font-size: 1.125rem;
        line-height: 1.75rem;

        span {
          &[class*="fa-"]{
            margin-right: 1rem;
            color: $yldGold;
            width: 1rem;
            text-align: center;
          }

          &.duration {
            &:after {
              content: ' days';
            }
          }
          &.duration {
            span.fas {
              color: #ddd;
            }
          }
          &.collateral {
            span.fas {
              color: $blue;
            }
          }
          &.interest {
            span.fas {
              color: $green;
            }
          }
          &.dollars {
            opacity: 0.7;
            font-weight: 300;
            padding-left: 2rem;
            font-size: 0.875rem;
            line-height: 1.75rem;

            &:before {
              content: '$';
            }
            span.fas {
              color: #fff;
              display: none;
            }
          }
        }
      }

      a.loan {
        position: relative;
        display: flex;
        justify-content: flex-end;
        text-decoration: none;

        > .reward,
        > .interest {
          transition: all 0.2s ease-out;
        }

        &.lending-offer {
          .reward {
            background: $yldGold;
            color: $yldGrey;
            border-radius: 0.5rem;
            padding: 0.75rem 0.5rem;
            width: 6rem;

            display: flex;
            flex-direction: column;
            align-items: center;
            position: absolute;
            left: 0;
            top: 0;
            transform: translateY(-.5rem);

            span {
              display: inline-block;

              &.amount {
                font-weight: 700;
                font-size: 1.25rem;
                &:after {
                  content: ' YLD';
                }
              }
              &.value {
                font-size: 0.875rem;
                opacity: 0.75;
                margin-top: 0.25rem;
                font-weight: 700;
              }
            }
          }
        }

        &.borrow-request {
          .interest {
            background: $yldGold;
            color: $yldGrey;
            border-radius: 0.5rem;
            padding: 0.75rem 0.5rem;
            width: 6rem;

            display: flex;
            flex-direction: column;
            align-items: center;
            position: absolute;
            left: 0;
            top: 0;
            transform: translateY(-.5rem);

            span {
              display: inline-block;

              &.amount {
                font-weight: 700;
                font-size: 1.25rem;
                &:after {
                  content: '%';
                }
              }
              &.value {
                font-size: 0.875rem;
                opacity: 0.75;
                margin-top: 0.25rem;
                font-weight: 700;
              }
            }
          }

          .reward {
            p:not([class]) {
              &:after {
                content: ' YLD';
              }
            }
          }
        }

        .info {
          background: lighten(#181818,2.5%);
          border-radius: 0.5rem;
          border: 1px solid rgba(#fff,0.05);
          width: calc(100% - 5.5rem);
          padding: 0.75rem;
          padding-left: 1.75rem;
          transition: all 0.2s ease-out;

          display: grid;
          grid-gap: 1rem;
          grid-template-columns: 3fr 3fr 1.5fr 2rem;
          align-items: stretch;

          font-size: 1.125rem;
          line-height: 1.75rem;

          > div {
            text-align: left;
            position: relative;
            display: flex;
            justify-content: center;
            flex-direction: column;

            &:not(.duration):not(.arrow){
              padding-right: 1rem;
            }

            &:not(:first-child) {

              &:after {
                content: '';
                display: block;
                height: 85%;
                width: 1px;
                position: absolute;
                left: -1.25rem;
                top: 50%;
                transform: translateY(-50%);
                background: rgba(#fff,0.2);
              }
            }
          }

          h3 {
            margin: 0;
            font-weight: 600;
            font-size: 0.75rem;
            letter-spacing: 0.05rem;
            opacity: 0.75;
            text-transform: uppercase;
            margin-top: 0.5rem;
            line-height: 1rem;
            color: $yldGold;
          }

          p {
            margin: 0;

            &:not([class]){
              color: #fff;
              font-weight: 600;
            }

            &.dollars {
              font-size: 0.875rem;
              font-weight: 600;
              color: #fff;
              opacity: 0.65;
              line-height: 1rem;

              &:before {
                content: '$';
              }
            }
          }
        }

        .principal {
          p {

          }
        }
        .interest {
          p:not([class]) {
            &:after {
              content: '%';
            }
          }
        }
        .duration {
          p {
            &:after {
              content: ' days';
            }
          }
        }

        .arrow {
          display: flex;
          align-items: center;
          color: $yldGold;
          justify-content: flex-end;
          font-size: 2rem;
          padding: 0;

          span.fas {
            margin: 0;
            transition: all 0.2s ease-out;
          }

          &:after {
            display: none !important;
          }
        }

        &:hover,
        &:focus {
          > .reward,
          > .interest {
            transform: translateY(-0.25rem);
          }
          .info {
            background: lighten(#181818,5%);
          }
          .arrow {
            span.fas {
              transform: translateX(0.25rem);
            }
          }
        }
      }

    }
  }

  @keyframes dash {
    to {
      stroke-dashoffset: 223;
      opacity: 0;
    }
  }

  linearGradient {
    display:none;
  }
  path {
    stroke:#eebc83;
  }
  svg linearGradient {
    display:initial;
  }
  #svg-text path {
    stroke:url(#linear-gradient);
  }
  #svg-text:hover #linear-gradient2 {
    display:initial;
  }
  #svg-text:hover path {
    stroke:url(#linear-gradient2);
  }
}

.bottom {
  width: 100%;
  padding: 2rem 2rem;

  h2 {
    font-size: 2.5rem;
    font-weight: 500;
    margin: 0 0 2.5rem 0;
    color: #fff;
    text-align: center;

    span {
      animation-name: blink;
      animation-duration: 1.25s;
      animation-iteration-count: infinite;
      animation-direction: alternate-reverse;
      animation-timing-function: linear;
      animation-fill-mode: forwards;
      animation-delay: none;

      @keyframes blink {
        0% {
          opacity: 1;
        }
        35% {
          opacity: 1;
        }
        40% {
          opacity: 0;
        }
        60% {
          opacity: 0;
        }
        65% {
          opacity: 1;
        }
        100% {
          opacity: 1;
        }
      }
    }
  }

  .links {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column-reverse;
    align-items: center;
    justify-content: space-around;

    a {
      font-family: inherit;
      border-radius: 0.25rem;
      border: 1px solid #fff;
      text-transform: uppercase;
      color: #fff;
      font-weight: 500;
      width: 100%;
      padding: 1.5rem 1rem;
      text-decoration: none;
      text-align: center;
      font-size: 1.5rem;
      transition: all 0.15s ease-out;

      &:last-child {
        margin-bottom: 2rem;
      }

      &:hover,
      &:focus {
      }

      &:last-child {
        background: rgba($yldGold,1);
        border-color: $yldGold;
        color: $yldGrey;

        &:hover,
        &:focus {
          background: rgba(lighten($yldGold,3%),1);
          box-shadow: 0 0 1rem rgba($yldGold,0.25);
        }
      }

      &:first-child {
        background: rgba(#fff,0);
        border-color: #888;

        &:hover,
        &:focus {
          background: rgba(#fff,0.05);
          text-shadow: 0 0 0.25rem rgba(#000,0.5);
          box-shadow: 0 0 1rem rgba(#ddd,0.125);
        }
      }
    }
  }
}
